.point {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    text-align: center;
}

.status {
    display: inline-block;
    padding: 2px 8px;
    border-radius: 4px;
    color: #ffffff;
}

.point--Unavailable,
.point--Timeout,
.status--Unavailable,
.status--Timeout {
    background-color: #b83030;
}
.point--Ok,
.status--Ok {
    background-color: #39c55c;
}
.point--Connecting,
.status--Connecting {
    background-color: #d9e443;
}
.point--HighPing,
.status--HighPing {
    background-color: #d38e33;
}
.point--Unknown,
.status--Unknown {
    background-color: #919191;
}

.content {
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.content__col1 {
    flex: 0 0 60%;
}

.content__col2 {
    flex: 0 0 40%;
}

.content__col1,
.content__col2 {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

@media (max-width: 900px) {
    .content {
        flex-wrap: wrap;
    }
    .content__col1,
    .content__col2 {
        flex: 0 0 100%;
    }
    .content__col2 {
        order: -1;
    }
}

.card__title {
    padding: 4px 8px;
}

.globalCard {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #39c55c22 !important;
}

.globalCard--green {
    background-color: #39c55c22 !important;
}
.globalCard--yellow {
    background-color: #bcc53922 !important;
}
.globalCard--red {
    background-color: #c5423922 !important;
}

.globalCard__status span {
    font-size: 22px;
}

.card {
    padding: 10px;
}
.settingsCard__ipInput {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
}
.settingsCard__ipInput *:first-child {
    flex: 1 1 auto;
}
.settingsCard__inputs {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.settingsCard__inputs > *:last-child {
    align-self: flex-end;
}
